@import url('https://fonts.googleapis.com/css?family=Josefin+Sans:300i|Martel+Sans|Montserrat:900i');

body {
  position: relative;
  font-family: "Martel Sans";
  color: #1D142E;
  margin: 0;
  background-color: #ffffff;

  overflow-x: hidden;
}

.hidden {
  display: none;
}

p {
  font-family: "Martel Sans";
  color: #1D142E;
}

section {
  height: 100vh;
}

.logo {
  font-size: 3.3em;
  font-family: Montserrat;
  font-weight: 900;
  font-style: oblique;
  display: block;
  text-align: center;
  /* color: #541a8b; */
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Josefin Sans";
  font-weight: 300;
  font-style: oblique;
  margin: 0;

}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  width: 100%;
}

.app>* {
  -ms-flex: 1 1 100%;
      flex: 1 1 100%;
}

div.img-card {
  position: relative;
  display: grid;

}

div.img-card2 {
  position: relative;
  display: inline-block;
  height: 24vw;
  padding: 3px 5px;
}

.img-look {
  width: 24vw;
  margin: 0;
  padding: 0;
}

.img-look2 {
  height: 24vw;
  margin: 0;
  padding: 0;
}

.img-look-modal {
  width: auto;
  max-height: 50vh;
  margin: 0;
  padding: 0;
}

.img-icon {
  width: 35px;
  height: 35px;
  position: absolute;
  top: 50%;
  left: 50%;
  opacity: 0;
}

.img-card:hover>.img-icon {
  opacity: 1;
}

.img-card2:hover>.img-icon {
  opacity: 1;
}

/* fallback for earlier versions of Firefox */

@supports not (flex-wrap: wrap) {
  div.masonry {
    display: block;
  }

  div.masonry img {
    display: inline-block;
    vertical-align: top;
  }
}

input {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-radius: 0;
}

*:focus {
  outline: none;
}

select {
  width: 100%;
  padding: 16px 20px;
  border: none;
  border-radius: 4px;
  background-color: #f1f1f1;

}

.clear {
  clear: both;
}

/*nav-bar*/
.navBar {
  background-color: #EFFAEE;
  width: 100%;
  height: 100%;
  display: block;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  text-align: center;
  z-index: 80;

}

.navBar h1 {
  margin: 0;
  display: inline-block;
  text-align: center;
}

.navBar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: inline-block;
}

.navLinks {
  float: left;
}

.navLinks a {
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li.navLinks.nav-right {
  float: right;
}

/* Nav transition effects */
.fade-enter {
  opacity: 0;
  z-index: 1;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in;
  -o-transition: opacity 250ms ease-in;
  transition: opacity 250ms ease-in;
}

.header {
  background-color: #EFFAEE;
  width: 100%;
  padding-top: 20px;
}

.headerImg {
  height: 45vh;
  display: block;
  margin: 0 auto;
}

.center {
  display: block;
  margin: 0 auto;
  text-align: center;
}

.left {
  float: left;
}

.looks-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.masonryApi {

  background-color: #EC4068;
  background: -webkit-linear-gradient(top, #F45F55, #EC4068, #AE2E7A);
  background: -o-linear-gradient(top, #F45F55, #EC4068, #AE2E7A);
  background: -webkit-gradient(linear, left top, left bottom, from(#F45F55), color-stop(#EC4068), to(#AE2E7A));
  background: linear-gradient(to bottom, #F45F55, #EC4068, #AE2E7A);

  display: -ms-flexbox;

  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  padding: 0px;
  height: 200vh;
  width: 120vw;

}

.masonryApi2 {
  background-color: #EC4068;
  background: -webkit-linear-gradient(top, #F45F55, #EC4068, #AE2E7A);
  background: -o-linear-gradient(top, #F45F55, #EC4068, #AE2E7A);
  background: -webkit-gradient(linear, left top, left bottom, from(#F45F55), color-stop(#EC4068), to(#AE2E7A));
  background: linear-gradient(to bottom, #F45F55, #EC4068, #AE2E7A);
  max-height: 125%;
  min-height: 50vh;
  width: 100vw;
  padding-top: 10px;
  text-align: center;
}

div.masonryApi:hover .img-look {
  opacity: 1;
}

div.masonryApi:hover .img-look:hover {
  opacity: 0.5;
}

div.masonryApi2:hover .img-look2 {
  opacity: 1;
}

div.masonryApi2:hover .img-look2:hover {
  opacity: 0.5;
}

.look-card {
  padding: 75px 30px;
}

.quoteBar {

  background-image: url("../images/bg01.jpg");
  background-size: cover;
  -ms-flex-order: 4;
      order: 4;

}

.quote-screen {
  background-color: #ce93d8;
  height: 100%;
  opacity: .5;
  z-index: 1;
}

.quoteBar blockquote {
  z-index: 10;
}

.component-heading {
  padding: 10px 0 0 0;
  text-align: center;
}

.footerBar {
  background-color: #EFFAEE;
  position: fixed;
  bottom: 0;
  width: 100%;
  -ms-flex-order: 6;
      order: 6;

  text-align: center;

}

.landing {
  -ms-flex-order: 1;
      order: 1;
}

.link:hover {
  cursor: pointer;
}

.FilterBar {
  padding: 20px;
  text-align-last: center;
}


